.feedback_section {
  position: relative;
  width: 100%;
  overflow: hidden;
  color: $black_color;
  z-index: 3;
  &::before {
    @include before_fig(100%, 100%, #ecf1f3);
    opacity: 0.55;
    z-index: 1;
  }
  img {
    position: absolute;
    top: 0;
    left: 0;
  }
  .feedback_wrap {
    position: relative;
    z-index: 3;
    width: 100%;
    max-width: rem(1200px);
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
  }
}


.feedback_text {
  width: 50%;
  margin-top: rem(140px);
  h2 {
    font-size: rem(40px);
    font-weight: 400;
  }
  p {
    margin-top: rem(40px);
    font-family: "Proxima Nova";
    max-width: rem(380px);
    font-size: rem(17px);
    font-weight: 400;
    line-height: 1.64;
  }
}













